class SelectionTrigger {
  private trigger?: string;

  public get() {
    return this.trigger;
  }

  public set(trigger?: string) {
    this.trigger = trigger;
  }
}

const selectionTrigger = new SelectionTrigger();

export default {
  get: () => {
    return selectionTrigger.get();
  },
  set: (trigger?: string) => {
    selectionTrigger.set(trigger);
  }
}
